import {Link as RouterLink} from "react-router-dom";
import {
    Avatar,
    Button, Divider,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Skeleton
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import useEarnInfo from "../hooks/useEarnInfo";

export default function Earn() {

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

        <Grid item xs={12}><Divider>COINEUS</Divider></Grid>
        <EarnSummary
            contract_address="0xAEC89F494E9afd1978590d2cD5Dcc4067bB8789D"
            network="fuse"
        />

        <Grid item xs={12}><Divider>COMMUNITY</Divider></Grid>
        <EarnSummary
            contract_address="0x225b81A6aCaF6448d74e30190375b12970D5Fa30"
            network="fuse"
        />

    </Grid>
}

function EarnSummary(props) {

    const {contract_address, network} = props;

    const {farmInfo, loading} = useEarnInfo(contract_address, network);

    if(loading){
        return <Grid item xs={12}>
            <Paper style={{padding:20}}>
                <Skeleton />
                <Skeleton />
            </Paper>
        </Grid>
    }

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar style={{marginRight: 10}}>
                        <Avatar src={`https://coineus.app/assets/tokens/${network}/${farmInfo.token0}/logo.png`}/>
                    </ListItemAvatar>
                    <ListItemText
                        primary={CoineusCryptoFormat(farmInfo.totalStaked)}
                        secondary="Total Staked"
                    />
                    <ListItemText
                        primary={farmInfo.apy.toFixed(2) + "%"}
                        secondary="Est. APY"
                        primaryTypographyProps={{textAlign:'right'}}
                        secondaryTypographyProps={{textAlign:'right'}}
                    />
                </ListItem>

                <ListItem>
                    <Button
                        variant="contained"
                        fullWidth
                        component={RouterLink} to={`/earn/${network}/${contract_address}`}
                    >VIEW</Button>
                </ListItem>

            </List>
        </Paper>
    </Grid>
}